<template>
  <div>
    <v-chart class="site-chart" :option="option" />
  </div>
</template>

<script>
import { use } from "echarts/core";
import { CanvasRenderer } from "echarts/renderers";
import { PieChart, BarChart } from "echarts/charts";
import {
  TitleComponent,
  TooltipComponent,
  LegendComponent
} from "echarts/components";
import VChart from "vue-echarts";
// import { THEME_KEY } from "vue-echarts";
import { ref, defineComponent } from "vue";
import utils from '@/utils/utils'
import apis from '@/apis/apis'

use([
  CanvasRenderer,
  PieChart,
  BarChart,
  TitleComponent,
  TooltipComponent,
  LegendComponent
]);

export default defineComponent({
  name: "AlertSiteStatsChart",
  components: {
    VChart
  },
  provide: {
    // [THEME_KEY]: "dark"
  },
  setup() {
    const option = ref({
      title: {
        show: false,
        text: "",
        left: "center"
      },
      tooltip: {
        trigger: "item",
        formatter: "{b} : {c} ({d}%)"
      },
      legend: {
        show: false,
        orient: "vertical",
        left: "left",
        // data: ["Direct", "Email", "Ad Networks", "Video Ads", "Search Engines"]
      },
      series: [
        {
          name: "",
          type: "pie",
          radius: "65%",
          center: ["50%", "50%"],
          data: [
            { value: 335, name: "Direct" },
            { value: 310, name: "Email" },
          ],
          emphasis: {
            itemStyle: {
              shadowBlur: 10,
              shadowOffsetX: 0,
              shadowColor: "rgba(0, 0, 0, 0.5)"
            }
          }
        }
      ]
    });

    return { option };
  },
  mounted() {
    console.log('onMounted')
    // setTimeout(()=>{this.option.title.text='Hello world'}, 5000)
    let endpoint = 'alert/alerts2/groupby/?by=channel__site&count=id'
    apis.apiAjaxGet(endpoint, {},
      (res) => {
        if (res.code === 0 && res.data) {
          let data = res.data.map(v=>{return {value: v.count_id, name: v.channel__site}})
          this.option.series[0].data = data
        }
      },
      utils.defaultApiErrorHandler(endpoint, ``)
    )
  }
});
</script>

<style scoped>
.site-chart {
  /* height: 100vh; */
  height: 300px;
  width: 100%;
}
</style>
