<template>
  <div>
    <v-chart class="type-bar-chart" :option="option" />
  </div>
</template>

<script>
import { use } from "echarts/core";
import { CanvasRenderer } from "echarts/renderers";
import { PieChart, BarChart } from "echarts/charts";
import {
  TitleComponent,
  TooltipComponent,
  LegendComponent,
  GridComponent
} from "echarts/components";
import VChart from "vue-echarts";
// import { THEME_KEY } from "vue-echarts";
import { ref, defineComponent } from "vue";
import utils from '@/utils/utils'
import apis from '@/apis/apis'

use([
  CanvasRenderer,
  PieChart,
  BarChart,
  TitleComponent,
  TooltipComponent,
  LegendComponent,
  GridComponent
]);

export default defineComponent({
  name: "AlertTypeStatsChartBar",
  components: {
    VChart
  },
  provide: {
    // [THEME_KEY]: "dark"
  },
  setup() {
    const option = ref({
      title: {
        show: false,
        text: "",
        left: "center"
      },
      tooltip: {
        trigger: "item",
        formatter: "{b} : {c} ({d}%)"
      },
      legend: {
        show: false,
        orient: "vertical",
        left: "left",
        // data: ["Direct", "Email", "Ad Networks", "Video Ads", "Search Engines"]
      },
      xAxis: {
        type: 'category',
        data: ['Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat', 'Sun']
      },
      yAxis: {
        type: 'value'
      },
      series: [
        {
          data: [120, 200, 150, 80, 70, 110, 130],
          type: 'bar',
          emphasis: {
            itemStyle: {
              shadowBlur: 10,
              shadowOffsetX: 0,
              shadowColor: "rgba(0, 0, 0, 0.5)"
            }
          }
        }
      ]
    });

    return { option };
  },
  mounted() {
    console.log('onMounted')
    // setTimeout(()=>{this.option.title.text='Hello world'}, 5000)
    let endpoint = 'alert/alerts2/groupby/?by=algorithm__name_ch&count=id'
    apis.apiAjaxGet(endpoint, {},
      (res) => {
        if (res.code === 0 && res.data) {
          this.option.series[0].data = res.data.map(v=>v.count_id)
          this.option.xAxis.data = res.data.map(v=>v.algorithm__name_ch)
        }
      },
      utils.defaultApiErrorHandler(endpoint, ``)
    )
  }
});
</script>

<style scoped>
.type-bar-chart {
  /* height: 100vh; */
  height: 300px;
  width: 100%;
}
</style>
