<template>
  <SimpleModalDlg :modalId="modalId" :title="` -  ${cno} - ${channelName}`" content="" defaultButtonText="" button2Text=""
    :button2Action="button2Action" :dialogWidth="1100">
    <template v-slot:bodyPost>
      <div class="container-fluid">
        <!-- <div class="row"> -->
        <div class="row">
          <div class="col-4">
            <div class="w-100 overflow-auto border-top border-bottom" style="height: 620px;">
              <div class="list-group" id="list-tab" role="tablist">
                <a 
                  v-for="item, i in Object.keys(channelAlgInfo)" :key="'alg' + modalId + i"
                  :class="'list-group-item list-group-item-action ' + ((i===0) ? 'active' : '')"
                  :id="`${modalId}-list-${item}`"
                  data-toggle="list"
                  :data-x="item"
                  role="tab"
                >
                  <div class="icon-name">
                    <div class="mr-2" style="max-height: 100%; width: 60px;">
                      <img :src="channelAlgInfo[item].icon" alt="">
                    </div>
                    <div style="width: 90px;">
                      <p class="mb-1"><b>{{channelAlgInfo[item].name}}</b></p>
                      <p class="my-0">
                        <svg :class="`icon svg-icon ${channelAlgInfo[item].channelAlgorthm ? 'dagou-icon' : 'dacha-icon'}`" aria-hidden="true">
                          <use :xlink:href="`#${channelAlgInfo[item].channelAlgorthm ? 'icon-dagouyouquan' : 'icon-cuowuguanbiquxiao-yuankuang'}`"></use>
                        </svg>
                        {{channelAlgInfo[item].channelAlgorthm ? '' : ''}}
                      </p>
                    </div>
                  </div>
                </a>
                <!-- <a class="list-group-item list-group-item-action" id="list-profile-list" data-toggle="list" href="#list-profile" role="tab" aria-controls="profile">Profile</a>
                <a class="list-group-item list-group-item-action" id="list-messages-list" data-toggle="list" href="#list-messages" role="tab" aria-controls="messages">Messages</a>
                <a class="list-group-item list-group-item-action" id="list-settings-list" data-toggle="list" href="#list-settings" role="tab" aria-controls="settings">Settings</a> -->
              </div>
            </div>
          </div>
          <div class="col-8 pl-0">
            <div class="w-100 overflow-auto border-top border-bottom container-fluid" style="height: 620px;">
              <div class="row w-100 ml-1 py-2 border-bottom">
                <!-- <p v-if="channelAlgInfo[currAlgName].channelAlgorthm" class="my-0">
                  <button class="btn btn-primary"></button>
                </p>
                <p v-else class="my-0">
                  <button class="btn btn-primary"></button>
                </p> -->
                <label class="mt-1 field-label"></label>
                <span class="mt-1 mr-4">{{channelAlgInfo[currAlgName].name}}</span>
                <label class="mt-1 field-label" for="id-channel-alg-config"></label>
                <input class="ml-1" type="radio" id="id-channel-alg-config" name="channelAlgConfig" v-model="algStatus[currAlgName].configured" value="1">
                <label class="ml-2 mt-1 field-label" for="id-channel-alg-unconfig"></label>
                <input class="ml-2" type="radio" id="id-channel-alg-unconfig" name="channelAlgConfig" v-model="algStatus[currAlgName].configured" value="0">
              </div>
              <div v-show="algStatus[currAlgName].configured === '1'" class="row mt-2 pb-2 border-bottom">
                <span class="field-label pl-3"></span>
                <div class="container-fluid">
                  <div class="row">
                    <div class="col-3 text-right">
                      <span class="">()</span>
                    </div>
                    <div class="col">
                      <input class="ml-2" v-model.number="algStatus[currAlgName].analyze_interval" type="number" step="500" />
                      <span class="">(5001000)</span>
                    </div>
                  </div>
                  <div class="row mt-1">
                    <div class="col-3 text-right">
                      <span class="">()</span>
                    </div>
                    <div class="col">
                      <input class="ml-2" v-model.number="algStatus[currAlgName].alert_interval" type="number" step="1" />
                    </div>
                  </div>
                  <div class="row mt-1">
                    <div class="col-3 text-right">
                      <span class="">(0-1)</span>
                    </div>
                    <div class="col">
                      <input class="ml-2" v-model.number="algStatus[currAlgName].alert_threshold" type="number" step="0.05" />
                      <span class="">()</span>
                    </div>
                  </div>
                  <!-- <div class="row mt-1">
                    <div class="col-4 text-right">
                      <span class="">(1-7)</span>
                    </div>
                    <div class="col">
                      <input class="ml-2" v-model.number="algStatus[currAlgName].alert_week_start" type="number" step="1"  style="width: 5em;"/>
                      ~
                      <input class="ml-2" v-model.number="algStatus[currAlgName].alert_week_end" type="number" step="1"  style="width: 5em;"/>
                    </div>
                  </div>
                  <div class="row mt-1">
                    <div class="col-4 text-right">
                      <span class="">(0-24)</span>
                    </div>
                    <div class="col">
                      <input class="ml-2" v-model.number="algStatus[currAlgName].alert_day_start" type="number" step="1"  style="width: 5em;"/>
                      ~
                      <input class="ml-2" v-model.number="algStatus[currAlgName].alert_day_end" type="number" step="1"  style="width: 5em;"/>
                    </div>
                  </div> -->
                </div>
              </div>

              <div v-show="algStatus[currAlgName].configured === '1'" class="row mt-2 pb-2 border-bottom">
                <div class="container-fluid">
                  <div class="row mb-1">
                    <span class="field-label pl-3 pt-1"></span>
                    <a class="btn-sm btn-primary" data-toggle="collapse" href="#dateTimeSetting" role="button" aria-expanded="false" aria-controls="dateTimeSetting">{{dateTimeSettingShown ? '' : ''}}</a>
                  </div>
                  <div class="row">
                    <div class="col collapse" id="dateTimeSetting">
                      <div class="border w-100" style="height: 350px;">
                        <WeekDatetimePicker v-model="algStatus[currAlgName].alert_times"></WeekDatetimePicker>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
              
              <div v-show="algStatus[currAlgName].configured === '1'" class="row mt-2 border-bottom">
                <div class="container-fluid">
                  <div class="row">
                    <span class="field-label py-2 pl-3">(ROI)</span>
                    <label class="ml-4 pt-1 pl-3 mr-2 my-1" for="id-roi-whole-region"></label>
                    <input class="my-1" id="id-roi-whole-region" type="checkbox">
                  </div>
                  <div class="row mt-2">
                    <div class="col">
                      <div class="border w-100" style="height: 300px;">
                        <img src="../../assets/img/test-gongdi.jpeg" alt="" style="max-width: 100%; max-height: 100%">
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </template>
  </SimpleModalDlg>
</template>

<script>
import utils from '@/utils/utils'
import SimpleModalDlg from '@/components/utils/SimpleModalDlg'
import apis from '@/apis/apis'
import algorithmUtils from './algorithm-utils'
import $ from 'jquery'
import WeekDatetimePicker from '../utils/WeekDatetimePicker'

const defaultAlgParams = {
  analyze_interval: 1000,
  alert_interval: 30,
  alert_threshold: 0.5,
  alert_times: null
}

function initDefaultAlgStatus () {
  let status = {}
  Object.keys(algorithmUtils.algNameIcons).forEach(v=>{
    status[v] = {
      configured: '1',
      ...defaultAlgParams,
    }
  })
  return status
}

// const defautRoi = {
//   left: 0,
//   top: 0,
//   width: 0,
//   height: 0
// }

export default {
  name: 'ChannelAlgConfigDlg',
  components: {
    SimpleModalDlg,
    WeekDatetimePicker
  },
  data () {
    return {
      channelAlgInfo: utils.deepCopy(algorithmUtils.algNameIcons),
      currAlgName: Object.keys(algorithmUtils.algNameIcons)[0],
      algStatus: initDefaultAlgStatus(), // 
      dateTimeSettingShown: false
    }
  },
  props: {
    modalId: {
      type: String,
      required: true
    },
    channelId: {
      type: Number,
      required: true
    },
    cno: {
      type: Number,
      required: true
    },
    channelName: {
      type: String,
      required: true
    }
  },
  computed: {
  },
  methods: {
    button2Action () {
      let endpoint = `channel/channels/${this.channelId}/config_alg/`

      let data = this.algStatus

      apis.apiAjaxPost(endpoint, data,
        (res) => {
          if (res.code === 0) {
            alert(`${this.cno}`)
            this.$emit('channel-alg-saved')
          } else {
            alert(`${this.cno}(${res.code}): ${res.message}`)
          }
          // 
          this.loadChannelAlgorithms()
        },
        utils.defaultApiErrorHandler(endpoint, ``)
      )
    },
    loadChannelAlgorithms () {
      let endpoint = `channel/channelalgorithms2/?channel=${this.channelId}`

      apis.apiAjaxGet(endpoint, {},
        (res) => {
          if (res.code === 0) {
            // 
            for (let k in this.channelAlgInfo) {
              this.channelAlgInfo[k].channelAlgorthm = null
            }
            // 
            Object.keys(this.channelAlgInfo).forEach(v=>{
              let status = {
                configured: '0',
                ...defaultAlgParams
              }
              this.algStatus[v] = status
            })
            // 
            res.data.forEach((v) => {
              // Update channelAlgInfo
              if (v.algorithm.name in Object.keys(this.channelAlgInfo)) {
                alert(`: ${v.algorithm.name}`)
              } else {
                this.channelAlgInfo[v.algorithm.name].channelAlgorthm = v
                // 
                this.algStatus[v.algorithm.name] = this.recordToStatus(v)
              }
            })
            console.log('loadChannelAlgorithms2', this.algStatus)
          } else {
            console.log('')
          }
        },
        utils.defaultApiErrorHandler(endpoint, ``)
      )
    },
    recordToStatus (record) {
      let status = {}
      Object.keys(defaultAlgParams).forEach(v=>{
        status[v] = record[v]
      })
      status.configured = '1'
      return status
    }
  },
  watch: {
    channelId () {
      this.loadChannelAlgorithms()
    },
    currAlgName () {
    },
    algStatus: {
      handler () {
        // console.log('algStatus changed', this.algStatus)
      },
      deep: true
    }
  },
  mounted () {
    $('a[data-toggle="list"]').on('shown.bs.tab', (event) => {
      // console.log('shown.bs.tab')
      // console.log($(event.target), $(event.target).attr('data-x')) // newly activated tab
      // console.log(event.relatedTarget) // previous active tab
      this.currAlgName = $(event.target).attr('data-x')
    })
    $('#dateTimeSetting').on('hidden.bs.collapse', () => {
      // console.log('dateTimeSetting hidden')
      this.dateTimeSettingShown = false
    })
    $('#dateTimeSetting').on('shown.bs.collapse', () => {
      // console.log('dateTimeSetting shown')
      this.dateTimeSettingShown = true
    })
  }
}
</script>

<style>
.icon-name{
  display: flex;
  height: 50px;
  align-items: center;
  justify-content: left;
}
</style>
